.geojson-container {
  width: 28%;
  padding: 1%;
  right: 0;
  z-index: 10000;
  background: white;
  position: absolute;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  float: right;
  -webkit-box-shadow: 1px 1px 5px gray;
          box-shadow: 1px 1px 5px gray;
}
.geojson-button {
  margin-left:-40%;
  background:white;
  position: absolute;
}
.geojson-button:hover {
  background: #eee;
}
.geojson-button:focus {
  background: white;
}
.geojson-area {
  overflow-x: scroll;
  height: 100%;
}



